import SwiperCore, { Navigation } from 'swiper/core'
import 'swiper/swiper-bundle.css'

SwiperCore.use([Navigation])

export default {
  mounted() {
    this.swiper = new SwiperCore('.post-carousel', {
      loop: true,
      slidesPerView: 1,
      spaceBetween: 22,
      centeredSlides: true,
      navigation: {
        nextEl: '.post-carousel-nav .next',
        prevEl: '.post-carousel-nav .prev',
      },
      breakpoints: {
        540: {
          slidesPerView: 2,
        },
        800: {
          slidesPerView: 3,
        },
        1024: {
          slidesPerView: 4,
        },
        1200: {
          slidesPerView: 5,
        },
      },
    })
  },
}
